import { Button, Card, CardActions, CardContent, Grid, makeStyles, Typography } from '@material-ui/core';
import { ActiveEra } from './Types';

const useStyles = makeStyles({
  root: {
    minWidth: '98%',
    textAlign: 'left'
  },
  title: {
    fontSize: 18,
  },
  pos: {
    marginBottom: 12,
  },
});

export const ValidatorsStats = (props: { stash: String, activeEras: ActiveEra[]; }) => {
  const classes = useStyles();
  let sortedByBlock = props.activeEras
  let noDataLabel = 'No Data Available'
  let firstBlock: ActiveEra | undefined = undefined
  let lastBlock: ActiveEra | undefined = undefined
  let scoringPeriodText = ''

  const copyValidatorStatistics = () => navigator.clipboard.writeText(scoringPeriodText)

  if(props.activeEras.length > 0) {
    sortedByBlock = props.activeEras.sort((e1,e2) => e1.block - e2.block)
    firstBlock = sortedByBlock[0];
    lastBlock = sortedByBlock[sortedByBlock.length - 1];
    scoringPeriodText = `
    Validator Date: ${new Date(firstBlock!.date).toLocaleDateString()}-${new Date(lastBlock!.date).toLocaleDateString()}
    Description: I was an active validator from era/block ${firstBlock!.era}/${firstBlock!.block} to era/block ${lastBlock!.era}/${lastBlock!.block} with stash account ${props.stash}
    `
    return (
      <Grid container item lg={12}>
        <Card className={classes.root}>
          <CardContent>
            <Typography className={classes.title} color="textPrimary" gutterBottom>
              Scoring period text:
            </Typography>
            <Typography className={classes.pos} color="textSecondary">
              { scoringPeriodText }
            </Typography>
          </CardContent>
          <CardActions>
            <Button onClick={copyValidatorStatistics} size="small">Copy to clipboard</Button>
          </CardActions>
        </Card>
      </Grid>
    );
  } else {
    return (
      <Card className={classes.root}>
        <CardContent>
        <Typography className={classes.pos} color="textSecondary">
            { noDataLabel }
          </Typography>
        </CardContent>
      </Card>
    )
  }
};
